/* 🟢 World History Main Container */
.world-history-container {
    position: relative;
    width: 100%;
    height: 100vh;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    padding: 20px;
  }
  
  /* 🏠 Home & Navigation Buttons */
  .home-btn,
  .nav-btn {
    position: absolute;
    background: rgba(0, 0, 0, 0.7);
    color: white;
    border: none;
    padding: 12px;
    font-size: 22px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  
  .home-btn:hover,
  .nav-btn:hover {
    background: rgba(255, 255, 255, 0.8);
    color: black;
  }
  
  .home-btn {
    top: 20px;
    left: 20px;
  }
  
  .nav-btn.left {
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .nav-btn.right {
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  /* 🟢 Content Layout */
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
    padding: 30px;
    border-radius: 15px;
  }
  
  /* 📜 Text Section */
  .text-section {
    flex: 1;
    padding-right: 20px;
  }
  
  .text-section h1 {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .text-section p {
    font-size: 1.2rem;
    margin-bottom: 20px;
    line-height: 1.5;
  }
  
  /* 📌 Read More Button */
  .read-more-btn {
    background: red;
    color: white;
    font-size: 18px;
    padding: 12px 20px;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    transition: 0.3s;
  }
  
  .read-more-btn:hover {
    background: darkred;
  }
  
  /* 🖼️ Image Section */
  .images-section {
    flex: 1;
    display: flex;
    justify-content: center;
    gap: 30px;
  }
  
  /* 📸 Image Styling */
  .image-container {
    width: 160px;
    height: 200px;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    transition: transform 0.3s ease-in-out;
  }
  
  .image-container:hover {
    transform: scale(1.1);
  }
  
  .history-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
  }
  